@import 'normalize.css';

:root {
    --primary: #f1641e;
    --black: #000;
    --white: #fff;
    --text-color: #222222;

    --font-family: 'Graphik', sans-serif;

    // Default layout
    --default-layout-header-height: 104px;
    --default-layout-width: 1400px;
    --default-layout-horizontal-spacer: 24px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: var(--font-family);
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizespeed;
    color: var(--text-color);
    overflow-y: overlay;
}
option {
    font-weight: normal;
    display: block;
    white-space: nowrap;
    min-height: 1.2em;
    padding: 0px 2px 1px !important;
}

// Scrollbar CSS
html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}

html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.15);
}

html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}

button,
input,
[tabindex] {
    outline: none;
    border: none;
}

a {
    color: var(--text-color);
    text-decoration: none;
}

// Fonts emmbeded
@font-face {
    font-family: Graphik;
    src: url('/assets/fonts/Graphik-Regular-Web.woff2');
    font-weight: 400;
}

@font-face {
    font-family: Graphik;
    src: url('/assets/fonts/Graphik-Medium-Web.woff2');
    font-weight: 600;
}

@font-face {
    font-family: Graphik;
    src: url('/assets/fonts/Graphik-Medium-Web.woff2');
    font-weight: 700;
}

@font-face {
    font-family: Guardian-EgypTT;
    src: url('/assets/fonts/Judson-Regular.ttf');
    font-weight: 700;
}


// Custom tippy tooltip
body {
    .tippy-box {
        border-radius: 8px;
        font-weight: 600;
        font-size: 1.6rem;
        line-height: 2.2rem;
        background-color: rgba(84, 84, 84, 0.92);
    }

    .tippy-arrow {
        color: rgba(84, 84, 84, 0.92);
    }

    .tippy-content {
        padding: 8px 9px;
    }

    .tippy-box[data-placement^='bottom'] > .tippy-arrow:before {
        top: -8px;
    }
}
