// Menu wrapper
.menu-list {
    width: 224px;
    position: relative;
    .menu-item {
        margin-left: 0;
    }
}
.menu-list::before {
    content: "";
    top: 0;
    width: 12px;
    height: 12px;
    bottom: -6px;
    left: calc(50% - -80px);
    transform: translateY(-6px) rotate(225deg);

    position: absolute;

    z-index: 123;
    box-shadow: 1px 1px 2px  rgb(0 0 0 / 12%);
    background-color:#fff;
}

.menu-popper {
    padding-bottom: 8px;
}

.menu-body {
    overflow-y: auto;
}

// Menu items
.menu-item {
    width: 100%;
    justify-content: flex-start;
    border-radius: 0;
    padding: 11px 16px;
    line-height: 1.8rem;
    font-weight: 600;

    &.separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

// Header
.header {
    position: relative;
    flex-shrink: 0;
    height: 50px;
    margin-top: -8px;
}

.back-btn {
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
